/*----------------------------------------------------------------------------------------------------------------------------------------------------------------------------

                                                                              Home - Nav

----------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/

.home .home-nav > ul {
    list-style: none;
}
.home .home-nav .row {
    height: 100vh;
    min-height: 600px;
    overflow: hidden;
}
.mainHome + nav,
.mainHome ~ footer,
.mainHome ~ .Error404 {
    display: none;
}
.home .home-nav .leaveTop {
    transition: ease all 0.5s;
    transform: translateY(-50vh);
}
.home .home-nav .leaveCenter {
    transition: ease all 0.5s;
    transform: translateY(-100vh);
}

/*----------------------------------------------------------------------------------------------------------------------------------------------------------------------------

                                                                              Nav - navTop - bg-navTop

----------------------------------------------------------------------------------------------------------------------------------------------------------------------------*/

.home .navTop {
    overflow-y: none;
    position: relative;
    background: #1e1e1e;
    height: 100%;
    min-height: 200px;
    clip-path: polygon(0 0, 100% 0%, 101% 80%, 0% 100%);
}
.home .navTop::after{
    clip-path: polygon(0 20%, 100% 0, 100% 100%, 0 100%);
    background: var(--pri);
    display: block;
    content: '';
    position: relative;
    bottom: 21%;
    height: 100%;
    width: 100%;
    z-index: 2;
    transition: ease all .5s;
}
.home .navTop::before{
    clip-path: polygon(0 20%, 100% 0, 100% 100%, 0 100%);
    background: black;
    display: block;
    content: '';
    position: relative;
    bottom: -78%;
    height: 100%;
    width: 100%;
    z-index: 1;
}
.home .navTop:hover::after{
    transform: translate(100%, -20%);
}
.home .navTop ul {
    margin-top: 2vh;
    padding-left: 4vh!important;
}
.home .navTop-link {
    position: absolute;
    top: 15%;
}
.home .navTop .navTop-link button {
    transition: ease all .5s;
    background: var(--pri);
    box-shadow: inset 5px -10px 120px 0px rgb(30 30 30 / 39%), inset 0px 11px 101px -5px rgb(30 30 30 / 39%);
    text-shadow: 0 0 3px #1e1e1e;
    color: var(--comple1);
    font-size: 2.5vw;
    width: 30vw;
    border-radius: 0;
    border: none;
    padding: 1.5vw 0.5vw;
    clip-path: polygon(0 0, 100% 0%, 101% 80%, 0% 100%);
}
@media (min-width:2500px) {
    .home .navTop .navTop-link button {
        font-size: 2vw;
        padding: 1vw 0.5vw;
    }
}
@media (max-width:1080px) {
    .home .navTop .navTop-link button {
        font-size: 1.8rem;
        width: 80vw;
    }
}
.home .navTop .navTop-link button:hover,
.home .navTop .navTop-link button:focus,
.home .navTop .navTop-link button:active
 {
     box-shadow: none;
    text-shadow: none;
    background-color: var(--pri);
    transform: scale(1.1);
}
.home ul li .bg-navTop {
    position: absolute;
    bottom: 0;
    font-size: 2vh;
    background: #1e1e1e;
    height: 100%;
    user-select: none;
}
.home ul li .bg-navTop::after {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    background: #1e1e1e;
    opacity: .8;
    transition: ease all 0.5s;
    height: 110%;
    width: 110%;
    user-select: none;
}
.home ul li.navTop:hover .bg-navTop::after {
    opacity: 0;
}
.home .list-NavTop {
    list-style-type: decimal;
}
.home .list-NavTop li {
    position: relative;
    top: -16vw;
    padding-left: 2vh;
    font-size: 1.5vw;
    text-align: left;
    letter-spacing: 0.1vh;
}
@media (min-width:2000px) {
    .home .list-NavTop li {
        top: -12vw;
        font-size: 1vw;
    }
}
@media (max-width:1450px) {
    .home .list-NavTop li {
        top: -21vw;
        font-size: 2vw;
    }
}
@media (max-width:1150px) {
    .home .list-NavTop li {
        top: -10vw;
        font-size: 2vw;
    }
}
@media (max-width:1150px) and (max-height:750px) {
    .home .list-NavTop li {
        top: -20vw;
        font-size: 2vw;
    }
}
@media (max-width:980px) {
    .home .list-NavTop li {
        top: -14vw;
        font-size: 2vw;
    }
}
@media (max-width:725px) {
    .home .list-NavTop li {
        top: -39vw;
        font-size: 1.5rem;
    }
}
@media (max-width:550px) {
    .home .list-NavTop li {
        top: -25vw;
        left: -5vw;
        font-size: 1rem;
    }
}
@media (max-width:430px) {
    .home .list-NavTop li {
        top: -19vw;
        left: -8vw;
        font-size: 0.8rem;
    }
}
@media (max-width:300px) {
    .home .list-NavTop li {
        top: -7vw;
        left: -9vw;
        font-size: 0.6rem;
    }
}
.home ul li .bg-navTop .typed {
    color: #e6e6e6;
}
