.headerAbout .row {
    overflow: hidden;
}
.headerAbout > .headerTitle{
    z-index: 1;
    position: absolute;
    top: 15%;
}
.headerAbout h1 {
    font-size: 3rem;
    background: var(--pri);
    clip-path: polygon(0 0, 100% 0%, 101% 80%, 0% 100%);
}
.headerAbout {
    overflow-y: none;
    position: relative;
    background: #1e1e1e;
    height: 100%;
    min-height: 200px;
    clip-path: polygon(0 0, 100% 0%, 101% 80%, 0% 100%);
}
.headerAbout ul {
    margin-top: 2vh;
    padding-left: 4vh!important;
}
.headerAbout .bg-navTop {
    position: absolute;
    bottom: 0;
    font-size: 2vh;
    background: var(--comple1);
    height: 100%;
    user-select: none;
}
.headerAbout .list-NavTop {
    list-style-type: decimal;
}
.headerAbout .list-NavTop li {
    position: relative;
    top: -16vw;
    padding-left: 2vh;
    font-size: 1.5vw;
    text-align: left;
    letter-spacing: 0.1vh;
}
@media (min-width:2000px) {
    .headerAbout .list-NavTop li {
        top: -12vw;
        font-size: 1vw;
    }
    .headerAbout {
        min-height: 400px;
    }
}
@media (max-width:1450px) {
    .headerAbout .list-NavTop li {
        top: -21vw;
        font-size: 2vw;
    }
}
@media (max-width:1150px) {
    .headerAbout .list-NavTop li {
        top: -10vw;
        font-size: 2vw;
    }
}
@media (max-width:1150px) and (max-height:750px) {
    .headerAbout .list-NavTop li {
        top: -20vw;
        font-size: 2vw;
    }
}
@media (max-width:1150px) and (max-height:1400px) {
    .headerAbout .list-NavTop li {
        top: -18vw;
        font-size: 2vw;
    }
}
@media (max-width:980px) {
    .headerAbout .list-NavTop li {
        top: -14vw;
        font-size: 2vw;
    }
}
@media (max-width:725px) {
    .headerAbout .list-NavTop li {
        top: -39vw;
        font-size: 1.5rem;
    }
}
@media (max-width:550px) {
    .headerAbout .list-NavTop li {
        top: -25vw;
        left: -5vw;
        font-size: 1rem;
    }
}
@media (max-width:430px) {
    .headerAbout .list-NavTop li {
        top: -19vw;
        left: -8vw;
        font-size: 0.8rem;
    }
}
@media (max-width:300px) {
    .headerAbout .list-NavTop li {
        top: -7vw;
        left: -9vw;
        font-size: 0.6rem;
    }
}
.headerAbout .bg-navTop .typed {
    color: #e6e6e6;
}
.headerAbout .bg-navTop li .blue1 {
    color: #9cdcfe;
}
.headerAbout .bg-navTop li .blue2 {
    color: #569cd6;
}
.headerAbout .bg-navTop li .purple {
    color: #a586b6;
}
.headerAbout .bg-navTop li .yellow {
    color: #dccd79;
}
.headerAbout .bg-navTop li .green1 {
    color: #438a55;
}
.headerAbout .bg-navTop li .green2 {
    color: #4ec9b0;
}
.headerAbout .bg-navTop li .red {
