}
.headerPortfolio {
    position: relative;
}
.headerPortfolio .row {
    overflow: hidden;
}
.headerPortfolio > .headerTitle{
    z-index: 1;
    position: absolute;
    bottom: 25%;
}
.headerPortfolio h1 {
    font-size: 3rem;
    background: var(--pri);
    clip-path: polygon(0 0, 100% 0%, 101% 80%, 0% 100%);

}
.headerPortfolio {
    overflow-y: none;
    position: relative;
    background: #fff;
    height: 120.5%;
    min-height: 200px;
    clip-path: polygon(0 0, 100% 0%, 101% 80%, 0% 100%);
}
.headerPortfolio .bg-navCenter .bg-fluid {
    width: 100vw;
    height: auto;
    position: sticky;
    top: 0;
    display: block;
}
.headerPortfolio .bg-navCenter .bg-browser {
    pointer-events: none;
    position: relative;
    height: 100%;
    background-color: #ffffff;
}
.headerPortfolio .bg-navCenter .bg-browser svg {
    margin: 0 0.3vw;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch {
    position: relative;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch input {
    pointer-events: none;
    border: 0.3vw solid #b9d5f8;
    border-radius: 2vw;
    background-color: #ffffff;
    width: 75vw;
    height: 3vw;
    margin: 0.5vw 0;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch input:focus {
    outline: none;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch > span {
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 0.8vw;
    padding-left: 3vw;
    font-size: 1.5vw;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch svg:nth-child(2) {
    position: absolute;
    left: 0;
    top: 0.8vw;
}
@media (max-width:767px) {
    .headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch input {
        width: 100vw;
        height: 5vw;
        position: sticky;
        top: 0;
        display: block;
    }
    .headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch > span {
        font-size: 3vw;
        padding-left: 4.5vw;
    }
    .headerPortfolio .bg-navCenter .bg-browser .bg-browserSearch svg:nth-child(2) {
    top: 1vw;
    font-size: 4vw!important;
    }
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserAccount {
    line-height: 0; 
    border: 0.3vh solid #e3e3e3;
    border-radius: 2vw;
    margin-left: 0.5vw;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserAccount svg {
    margin: 0;
}
.headerPortfolio .bg-navCenter .bg-browser .bg-browserAccount span {
    pointer-events: none;
    font-size: 1.5vw;
    margin-left : 0.5vw;
}
    .headerPortfolio .bg-navCenter .bg-browser svg:last-child {
    margin: 0;
}
.headerPortfolio .bg-navCenter .bg-browser:before {
    content: '';
    display: block;
    position: absolute;
    top: 4.5vw;
    height: 0.5%;
    width: 100%;
    border-bottom: 0.2vw solid rgba(0,0,0,.1);
}
