        width: 100%;
        height: 100%;
        color: #ffffff;
    }
    .headerContact .bg-navBottomPhone {
        position: absolute;
        right: 0;
        top: 0;
        background: 100% 28% / cover url(../media/phone.svg) no-repeat;
        height: 100%;
        width: 100%;
    }
}
